import { Fragment } from "react";
import { Helmet } from "react-helmet";
import { WalletCard } from "../../components/WalletCard/WalletCard";
import { ConstTranslate } from "../../constants/translate";
import UserContacts from "../../components/Contacts/UserContacts";
import ContactList from "../../components/Contacts/ContactList";


const Home = () => {
  return (
    <Fragment>
      <Helmet>
        <style>
          {`
            @font-face {
              font-family: "Consolas";
              font-weight: bold;
              font-style: normal;
              src: url(${process.env.PUBLIC_URL + "/assets/fonts/Consolas.eot"
            });
              src: url(${process.env.PUBLIC_URL + "/assets/fonts/Consolas.eot?#iefix"
            })
              format("embedded-opentype"),
              url(${process.env.PUBLIC_URL + "/assets/fonts/Consolas.woff2"})
              format("woff2"),
              url(${process.env.PUBLIC_URL + "/assets/fonts/Consolas.woff"
            }) format("woff"),
              url(${process.env.PUBLIC_URL + "/assets/fonts/Consolas.ttf"})
              format("truetype"),
              url(${process.env.PUBLIC_URL + "/assets/fonts/Consolas.svg#Consolas"
            })
              format("svg");
            }

            @font-face {
              font-family: "Merriweather-Black";
              font-weight: bold;
              font-style: normal;
              src: url(${process.env.PUBLIC_URL + "/assets/fonts/Merriweather-Black.eot"
            });
              src: url(${process.env.PUBLIC_URL +
            "/assets/fonts/Merriweather-Black.eot?#iefix"
            })
              format("embedded-opentype"),
              url(${process.env.PUBLIC_URL +
            "/assets/fonts/Merriweather-Black.woff2"
            })
              format("woff2"),
              url(${process.env.PUBLIC_URL + "/assets/fonts/Merriweather-Black.woff"
            }) format("woff"),
              url(${process.env.PUBLIC_URL + "/assets/fonts/Merriweather-Black.ttf"
            })
              format("truetype"),
              url(${process.env.PUBLIC_URL +
            "/assets/fonts/Merriweather-Black.svg#Merriweather-Black"
            })
              format("svg");
            }
          
          `}
        </style>
      </Helmet>
      <div className="space-pb--50">
        <WalletCard />
        <div className="backgrounds__white-top">
          <ContactList title={ConstTranslate.NEW_CONTACTS} filters={{ recentAdded: 1 }} count={5} />
          <UserContacts />
        </div>
      </div>
    </Fragment>
  );
};

export default Home;
