import { useSelector } from "react-redux"
import { ConstTranslate } from "../../constants/translate"
import { utilService } from "../../services/util.service"
import { userService } from "../../services/user.service"
import Contact from "../Contact/Contact"

const UserContacts = () => {
    const { currentUser } = useSelector(state => state.user)
    const mostRecentDate = utilService.getMostRecentObject(userService.getContactPurchaseType(currentUser))?.createdAt

    return (
        <div className="contact-list--wrapper">
            <div className="contact-list__sub-header">
                <span>{ConstTranslate.MY_ORDERS}</span>
                {mostRecentDate && <span>{utilService.getFormattedDate(new Date(mostRecentDate))}</span>}
            </div>

            {userService.getContactPurchaseType(currentUser).map((contactTransaction, idx) => {
                return <Contact key={idx} contact={contactTransaction.contact} />
            })}
        </div>
    )
}

export default UserContacts