import { AiOutlineArrowDown, AiOutlineArrowUp, AiOutlinePlus, AiOutlineStar } from "react-icons/ai";
import { MdOutlineFavoriteBorder } from 'react-icons/md'
import { useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { ReactComponent as PlusCreditIcon } from "../../assets/icon/plus_credit.svg"
import { ConstRoutes } from "../../constants/routes";
import { ConstTranslate } from "../../constants/translate";

export const WalletCard = () => {
    const { currentUser } = useSelector(state => state.user)
    const navigate = useNavigate()

    const onCredits = () => {
        navigate(ConstRoutes.CREDIT)
    }

    const links = [
        { url: ConstRoutes.CONTACTS_EXPENSES, icon: <AiOutlineArrowDown /> },
        { url: ConstRoutes.CONTACTS_INCOME, icon: <AiOutlineArrowUp /> },
        { url: ConstRoutes.CONTACTS_RECOMMANDED, icon: <AiOutlineStar /> },
        { url: ConstRoutes.WISHLIST, icon: <MdOutlineFavoriteBorder /> },
        { url: ConstRoutes.CREDIT, icon: <AiOutlinePlus />, class: 'plus' },
    ]

    return (
        <div className="wallet-card-section">
            <div className="wallet-card">
                <div className="balance">
                    <div className="left">
                        <h2 className="title">{ConstTranslate.CREDITS} <PlusCreditIcon /></h2>
                        <h1 className="total" onClick={onCredits}>{currentUser?.credits}$</h1>
                    </div>
                </div>
                <div className="wallet-footer scrollable-wrapper">
                    {
                        links.map(link => {
                            return (
                                <div className="item" key={link.url}>
                                    <Link to={link.url}>
                                        <div className={`icon-wrapper ${link?.class || ""}`}>
                                            {link.icon}
                                        </div>
                                    </Link>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        </div>
    )
}
